import React from 'react'
import "./note.css"

const Note = (props) => {
    const removeItem=()=>{
            props.delete(props.id)
    }
    return (
        <div className="note">
        <h1 className="note-h1"> {props.title}</h1>
        <br />
        <p className="note-p">{props.content} </p>
      
    <button className="note_button" onClick={removeItem}> 
    <i class="fas fa-trash-alt" ></i>
    </button>
        </div>
    )
}

export default Note